<template>
    <div class="partner-page " v-if="load">
        <div class="partner-page-image">
            <div class="overlay">
                <navbar />
                <h2 class="main-title">{{data.name}}</h2>
            </div>
        </div>
        <div class="partner-page-container container">
            <div class="row" v-scrollanimation>
                <div class="col-lg-6">
                    <div>
                        <h4 class="title">{{data.title1}}</h4>
                        <p class="disc">
                            {{data.text1}}
                        </p>
                    </div>
                    <div style="margin-top: 40px">
                        <h4 class="title">{{data.title2}}</h4>
                        <p class="disc">
                            {{data.text2}}
                        </p>
                    </div>
                </div>
                <div class="col-lg-6">
                    <img :src="data.image" style="width: 95%" :alt="data.name"/>
                </div>
            </div>
        </div>
        <custom-footer />
    </div>
    <loader3 v-else />
</template>

<script>
import Navbar from "@/components/Navbar.vue"
import CustomFooter from "@/components/CustomFooter.vue";
import Loader3 from "@/components/Loader3.vue"

export default {
    name: "Partner",
    components: {
        Navbar,
        CustomFooter,
        Loader3
    },
    data() {
        return {
            id: null,
            data: [],
            load: false,
            lang: '',
        };
    },
    methods:{
        getData() {
            this.lang = localStorage.getItem("lang");
            this.axios.get(this.$store.state.url + "partner_details?language=" + this.lang + "&id=" + this.id)
                .then(res => {
                    if (res.data.error == 'false') {
                        this.load = true
                        if(this.lang == "en")
                            this.data = res.data.data[0]
                        else 
                            this.data = res.data.data

                    }
                    else
                        this.$router.replace({ name: '404-page' })
                    console.log(this.data)
                });
        },
    },
    mounted() {
        document.documentElement.scrollTop = 0
        this.id = this.$route.params.id
        console.log(this.id)
        this.getData()
    }
};
</script>

<style>
.partner-page .partner-page-image {
    background-image: url(../assets/img/portfolio.jpg);
    position: relative;
    height: 80vh;
    background-position: 50%;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

.partner-page .overlay {
    height: 100%;
    width: 100%;
    background: linear-gradient(270deg, #126801b6, #15203bce);
}

.partner-page .main-title {
    text-align: center;
    font-weight: bold;
    color: #fff;
    margin-bottom: 60px;
    padding-top: 130px;
}

@media (min-width: 992px) {
    .partner-page .main-title {
        font-size: 50px;
    }
}

@media (max-width: 991px) {
    .partner-page .main-title {
        font-size: 30px;
    }
}


.partner-page .partner-page-container {
    padding: 100px 20px;
}

.partner-page .title {
    color: var(--main-blue);
    text-align: justify;
    font-weight: bold;
}

.partner-page .disc {
    text-align: justify;
    /* font-size: 17px; */
    margin-top: 20px
}




.partner-page .before-enter {
    opacity: 0;
    transform: translateY(150px);
    transition: all 2s;
}

.partner-page .enter {
    opacity: 1;
    transform: translateY(0px);
}


</style>
