<template>
    <div class="product-page" v-if="load">
        <div class="product-page-image">
            <div class="overlay">
                <navbar />
                <h2 class="main-title">{{data.name}}</h2>
            </div>
        </div>
        <div class="product-page-content container">
            <div class="row">
                <div class="col-lg-6">
                    <div class="row">
                        <div class="col-lg-6 img">
                            <img v-if="data.images[0] != null" :src="data.images[0]" :alt="data.name" />
                        </div>
                        <div class="col-lg-6 img">
                            <img v-if="data.images[1] != null" :src="data.images[1]"  :alt="data.name" />
                        </div>
                    </div>
                    <div class="row" style="margin-top: 20px">
                        <div class="col-lg-12 img">
                            <img v-if="data.images[2] != null" :src="data.images[2]" :alt="data.name"  />
                        </div>
                    </div>
                </div>
                <div class="col-lg-6 img">
                    <img v-if="data.images[3] != null" :src="data.images[3]" :alt="data.name"  />
                </div>
            </div>
            <div class="row">
                <div class="col-lg-9">
                    <div class="details">
                        <h4 class="title">
                            {{ data.title }}
                        </h4>
                        <p class="disc">
                            {{ data.text }}
                        </p>
                    </div>
                </div>
            </div>
            <div class="row button-row">
                <a :href="data.web_link" target="__blank" v-if="data.web_link != null">
                    <button class="visit-button">Visit Demo</button>
                </a>
                <a :href="data.app_link" target="__blank" v-if="data.app_link != null">
                    <button class="visit-button">Download App</button>
                </a>
            </div>
        </div>
        <order-product style="z-index:-1" />
        <custom-footer />
    </div>
    <loader3 v-else />
</template>

<script>
import Navbar from "@/components/Navbar.vue"
import CustomFooter from "@/components/CustomFooter.vue"
import Loader3 from "@/components/Loader3.vue"
import OrderProduct from "@/components/OrderProduct.vue"

export default {
    name: "product-page",
    components: {
        Navbar,
        CustomFooter,
        Loader3,
        OrderProduct
    },
    data() {
        return {
            data: null,
            load: false,
            product_id: null
        };
    },
    methods: {
        getData() {
            this.lang = localStorage.getItem("lang");
            this.axios.get(this.$store.state.url + "product_details?language=" + this.lang + "&product_id=" + this.product_id)
                .then(res => {
                    if (res.data.error == 'false') {
                        this.load = true
                        this.data = res.data.data
                    }
                    else
                        this.$router.replace({ name: '404-page' })
                    console.log(res.data.data)
                });
        },
    },
    mounted() {
        document.documentElement.scrollTop = 0;
        this.product_id = this.$route.params.id
        this.getData()
    }
};
</script>

<style lang="scss">
.product-page {
    position: absolute;
    z-index: 100;
}

.product-page .product-page-content {
    padding: 30px 50px;
    z-index: -1;
    position: relative;
}

.product-page .product-page-image {
    background-image: url(../assets/img/about-page.jpg);
    position: relative;
    height: 60vh;
    background-position: 50%;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    opacity: 90%;
}

.product-page .overlay {
    height: 100%;
    width: 100%;
    background: linear-gradient(270deg, #1268017c, #15203b96);
}

.product-page .product-page-image .main-title {
    text-align: center;
    font-weight: bold;
    color: #fff;
    margin-bottom: 60px;

}


@media (min-width: 992px) {
    .product-page .product-page-image .main-title {
        font-size: 50px;
        padding-top: 70px;
    }
}

@media (max-width: 991px) {
    .product-page .product-page-image .main-title {
        font-size: 40px;
        padding-top: 100px;
    }
}

.product-page .img {
    // background: #f4f4f4;
    display: flex;
    align-items: center;

}

.product-page img {
    width: 100%;
    height: 100%;
    border-radius: 15px;
}


.product-page .row {
    row-gap: 20px;
    // column-gap: 20px;
}

.product-page .details {
    text-align: justify;
    margin-top: 50px;
}

.product-page .title {
    color: var(--main-blue);
    font-weight: bold;
    margin: 60px 0 20px;
}

.product-page .disc {
    color: #666;
    font-size: 18px;
}

.product-page .logo-card {
    border: none;
    box-shadow: 0 0 20px rgb(0 0 0 / 17%);
    align-items: center;
    width: 420px;
    height: 250px;
    padding: 35px;
    border-radius: 20px;
}

.product-page .visit-button {
    position: relative;
    color: var(--main-blue);
    font-size: 16px;
    // font-weight: 800;
    text-decoration: none;
    margin: 30px 0;
    border: 1px solid var(--main-blue);
    padding: 12px 30px;
    overflow: hidden;
    transition: .6s all ease;
    background: transparent;
    border-radius: 10px;
    z-index: 10000;
}

.product-page .visit-button::before {
    background: var(--main-blue);
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    transition: all .6s ease;
}

.product-page .visit-button:hover {
    color: #fff;
}

.product-page .visit-button::before {
    width: 0%;
    height: 102%;
}

.product-page .visit-button:hover::before {
    width: 102%;
}
.product-page .button-row{
    text-align: justify;
    margin-top: 20px;
    column-gap: 20px;
}
html:lang(en) .product-page .button-row{
    margin-left: 5px;
}
html:lang(ar) .product-page .button-row{
    margin-right: 5px;
}
</style>
